n de datos, es esencial proceder al análisis. Este proceso puede incluir:")]),_vm._m(3),_vm._m(4),_c('h2',[_vm._v("Ventajas y limitaciones de las encuestas")]),_c('p',{staticClass:"mb-4"},[_vm._v("Las encuestas permiten recopilar grandes cantidades de datos de manera rápida y eficiente, y los resultados son cuantificables, lo que facilita su análisis estadístico y permite hacer generalizaciones (Alaminos-Fernández, 2023). Las encuestas presentan múltiples ventajas como método cuantitativo:")]),_vm._m(5),_c('p',{staticClass:"mb-4"},[_vm._v("A continuación, presentaremos algunos ejemplos de encuestas; estos ejemplos nos muestran cómo son herramientas útiles para recopilar datos numéricos sobre fenómenos sociales, permitiendo análisis estadísticos que ayudan a tomar decisiones informadas, en diferentes áreas.")]),_c('div',{staticClass:"row mb-4"},[_vm._m(6),_c('div',{staticClass:"col-12 col-xl-7",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"bg-amarillo p-4"},[_c('p',{staticClass:"mb-4"},[_vm._v("Las encuestas son herramientas esenciales en la investigación social, ya que permiten recopilar datos relevantes y significativos para analizar comportamientos, percepciones y tendencias en diferentes contextos. En el PDF Ejemplos de encuestas, se presentan estudios aplicados a temas como el impacto de las redes sociales en la salud mental de los jóvenes, la satisfacción laboral en empresas y el comportamiento electoral en comunidades locales. A través de estos ejemplos, se destacan metodologías, objetivos y resultados esperados que ilustran cómo estructurar y aprovechar encuestas para obtener información cuantitativa útil en la toma de decisiones y el desarrollo de estrategias basadas en evidencia.")]),_c('a',{staticClass:"d-flex me-auto w-fit bg-white box-shadow cursor-pointer",attrs:{"data-aos":"zoom-in","href":_vm.obtenerLink('/downloads/Anexos_Ejemplos de encuestas.pdf'),"target":"_blank"}},[_c('img',{staticClass:"h-100",staticStyle:{"width":"48px"},attrs:{"src":require("@/assets/componentes/pdf-icon-square.svg"),"alt":"Texto que describa la imagen"}}),_vm._m(7)])])])]),_vm._m(8),_c('p',{staticClass:"mb-4"},[_vm._v("El análisis de datos estadísticos es un proceso de organización, procesamiento e interpretación de los datos numéricos, recolectados a través de encuestas u otros métodos cuantitativos. Su finalidad es identificar patrones, realizar inferencias y validar hipótesis planteadas. Las estadísticas descriptivas, como la media, la mediana y la moda, son comunes para resumir los datos, mientras que las estadísticas inferenciales permiten hacer generalizaciones y evaluar relaciones entre variables. (Field, 2009)")]),_vm._m(9),_vm._m(10),_c('div',{staticClass:"row mb-4"},[_vm._m(11),_c('div',{staticClass:"col-12 col-xl-7",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"bg-amarillo p-4"},[_c('p',{staticClass:"mb-4"},[_vm._v("El análisis estadístico es una herramienta clave en las ciencias sociales, ya que permite interpretar datos, validar hipótesis y ofrecer información crítica para la toma de decisiones informadas. En el PDF Ejemplos de análisis estadístico, se exploran casos prácticos que ilustran el uso de técnicas estadísticas como la correlación de Pearson, pruebas t y regresión múltiple. A través de estos ejemplos, se muestra cómo identificar patrones en áreas como el acceso a la educación superior, la eficacia de programas sociales y la salud mental, contribuyendo al diseño de estrategias que respondan a las necesidades sociales de manera efectiva y basada en evidencia.")]),_c('a',{staticClass:"d-flex me-auto w-fit bg-white box-shadow cursor-pointer",attrs:{"data-aos":"zoom-in","href":_vm.obtenerLink('/downloads/Anexos_Ejemplos análisis estadístico.pdf'),"target":"_blank"}},[_c('img',{staticClass:"h-100",staticStyle:{"width":"48px"},attrs:{"src":require("@/assets/componentes/pdf-icon-square.svg"),"alt":"Texto que describa la imagen"}}),_vm._m(12)])])])]),_vm._m(13),_c('p',{staticClass:"mb-4"},[_vm._v("El proceso de validación de instrumentos en métodos cuantitativos, es fundamental para garantizar la precisión y confiabilidad de los resultados obtenidos en investigaciones. La validación busca confirmar que el instrumento mide, de manera efectiva y consistente, el fenómeno o constructo de interés. La validación de instrumentos incluye diversas etapas y tipos de validez (como la de contenido, constructo y criterio) que aseguran que el instrumento es adecuado para medir el fenómeno específico dentro del contexto de la investigación. (Anastasi y Urbina, 1997; Sarmiento, Botero & Carvajal, 2013).")]),_c('h2',[_vm._v("Fases del proceso de validación")]),_c('p',{staticClass:"mb-4"},[_vm._v("El proceso de validación consta de varias fases que se deben seguir de manera sistemática, para asegurar que el instrumento tenga la calidad necesaria para la recolección de datos confiables. Estas fases son:")]),_c('ImagenInfografica',{staticClass:"color-secundario mb-5 d-md-none",scopedSlots:_vm._u([{key:"imagen",fn:function(){return [_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-38.png"),"alt":"Texto que describa la imagen"}})])]},proxy:true}])},[_c('div',{staticClass:"tarjeta bg-amarillo p-4",attrs:{"x":"16%","y":"11%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Diseño y definición del instrumento")]),_c('p',[_vm._v("El proceso comienza con la conceptualización del constructo o fenómeno que se desea medir. Esta etapa implica realizar una revisión exhaustiva de la literatura existente, para establecer una definición clara y específica del constructo y, sobre esta base, desarrollar los ítems preliminares del instrumento. Los ítems deben alinearse claramente con los aspectos esenciales del concepto o fenómeno de interés. Quispe et al. (2023), explica que esta etapa es crucial para asegurar que el contenido inicial del instrumento, esté adecuadamente diseñado y relevante para la medición de las variables deseadas.")])]),_c('div',{staticClass:"tarjeta bg-amarillo p-4",attrs:{"x":"62.8%","y":"30.8%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Validez de contenido ")]),_c('p',[_vm._v("La validez de contenido se refiere a si los ítems del instrumento representan adecuadamente el constructo que se desea medir. Este tipo de validez suele evaluarse con la ayuda de un panel de expertos en el tema y la medición. Los expertos revisan los ítems para verificar si estos cubren todas las dimensiones importantes del constructo, sugiriendo ajustes o eliminaciones de aquellos elementos que no se consideran relevantes o que sean redundantes. Sarmiento, Botero & Carvajal,(2013), subrayan que la validez de contenido es esencial en los estudios cuantitativos, dado que asegura que el instrumento cuenta con una base sólida para la medición precisa de las variables.")])]),_c('div',{staticClass:"tarjeta bg-amarillo p-4",attrs:{"x":"16%","y":"50.3%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Prueba piloto y análisis estadístico inicial ")]),_c('p',[_vm._v("Una vez ajustados los ítems en función de la validación de contenido, el siguiente paso es aplicar el instrumento en una prueba piloto. La prueba piloto permite verificar la comprensión de los ítems por parte de los participantes y su relevancia para la medición del fenómeno. En esta fase, se utiliza una muestra representativa de la población objetivo para observar si existen problemas de interpretación o comprensión. Como señalan Muñiz & Fonseca-Pedrero (2019) y Hair et al. (2010), este paso ayuda a depurar el instrumento antes de aplicarlo a gran escala. Después de realizar los ajustes necesarios, se ejecutan análisis estadísticos preliminares, tales como el cálculo de la consistencia interna, mediante el coeficiente Alfa de Cronbach, para verificar la confiabilidad inicial del instrumento.")])]),_c('div',{staticClass:"tarjeta bg-amarillo p-4",attrs:{"x":"62.8%","y":"69%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Validez de constructo ")